@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;500&family=Quicksand:wght@700&family=Red+Hat+Display:wght@500;700&display=swap);
.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
